// js/renderer.js
class Renderer {
    constructor(canvas, gridResolution, cellSize) {
        this.ctx = canvas.getContext('2d');
        this.canvas = canvas;
        this.gridResolution = gridResolution;
        this.cellSize = cellSize;
        this.visualizationMode = 'normal';
        this.showClouds = true;
        this.CONTOUR_INTERVAL = 0.1;
        this.MAX_WATER_HEIGHT = 10.0; // Aumentado para coincidir con simulationCore

        this.lastWaterMap = null; // Para suavizado visual

        this._updateLegendVisibility(this.visualizationMode); // Initial legend setup
    }

    setVisualizationMode(mode) {
        this.visualizationMode = mode;
        this._updateLegendVisibility(mode);
    }

    setShowClouds(show) {
        this.showClouds = show;
    }

    _updateLegendVisibility(mode) {
        // Hide all legends first
        document.getElementById('normalLegend').style.display = 'none';
        document.getElementById('riskLegend').style.display = 'none';
        document.getElementById('infiltrationLegend').style.display = 'none';
        document.getElementById('waterOnlyLegend').style.display = 'none';
        document.getElementById('cloudsOnlyLegend').style.display = 'none';

        // Show relevant legends for the current mode
        switch (mode) {
            case 'normal':
                document.getElementById('normalLegend').style.display = 'block';
                document.getElementById('riskLegend').style.display = 'block'; // Risk legend also visible in normal mode
                break;
            case 'risks':
                document.getElementById('riskLegend').style.display = 'block';
                break;
            case 'infiltration_potential':
                document.getElementById('infiltrationLegend').style.display = 'block';
                break;
            case 'water_only':
                document.getElementById('waterOnlyLegend').style.display = 'block';
                break;
            case 'clouds_only':
                document.getElementById('cloudsOnlyLegend').style.display = 'block';
                break;
        }
    }

    // Maps Font Awesome class names to fallback emojis
    _getFontAwesomeChar(className, fallbackEmoji) {
        const iconMap = {
            'fas fa-city': '🏙️',
            'fas fa-tint': '💧',
            'fas fa-house-damage': '🏠',
            'fas fa-hill-avalanche': '🪨',
            'fas fa-mountain': '⛰️' // Used for landslide risk in main map
        };
        return iconMap[className] || fallbackEmoji;
    }

    draw(maps, settlements, settings) {
        const { heightMap, waterMap, cloudMap, riskMap } = maps;
        const { cloudinessFactor, baseInfiltrationRate, showClouds } = settings; // Pass showClouds from simulation settings

        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

        for (let y = 0; y < this.gridResolution; y++) {
            for (let x = 0; x < this.gridResolution; x++) {
                const idx = y * this.gridResolution + x;
                const terrain = heightMap[idx];
                const water = waterMap[idx];
                const risk = riskMap[idx];
                const cloudDensity = cloudMap[idx]; // Get cloud density for this cell

                switch (this.visualizationMode) {
                    case 'normal':
                        this._drawNormalMode(x, y, terrain, water, risk);
                        break;
                    case 'risks':
                        this._drawRiskMode(x, y, risk);
                        break;
                    case 'infiltration_potential':
                        this._drawInfiltrationPotentialMode(x, y, terrain, baseInfiltrationRate);
                        break;
                    case 'water_only':
                        this._drawWaterOnlyMode(x, y, water);
                        break;
                    case 'clouds_only':
                        this._drawCloudsOnlyMode(x, y, cloudDensity);
                        break;
                }
            }
        }

        // Overlay specific elements only in normal mode
        if (this.visualizationMode === 'normal') {
            this._drawContourLines(heightMap);
            if (this.showClouds) { // Use the 'showClouds' setting from controls
                this._drawClouds(cloudMap, cloudinessFactor);
            }
            // Overlay de derrumbes
            this._drawLandslideOverlay(maps.riskMap);
            this._drawSettlements(settlements, maps.riskMap);
        }

        // Guardar el mapa de agua anterior para suavizado
        this.lastWaterMap = new Float32Array(maps.waterMap);
    }

    _drawNormalMode(x, y, terrain, water, risk) {
        // Color base del terreno
        let r, g, b;
        if (terrain < 0.15) { r = 80; g = 120; b = 160; }
        else if (terrain < 0.3) { r = 120; g = 180; b = 100; }
        else if (terrain < 0.5) { r = 180; g = 170; b = 110; }
        else if (terrain < 0.7) { r = 150; g = 120; b = 80; }
        else if (terrain < 0.85) { r = 120; g = 90; b = 60; }
        else { r = 200; g = 200; b = 200; }
        this.ctx.fillStyle = `rgb(${r},${g},${b})`;
        this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);

        // Agua: gradiente y alpha para efecto realista
        if (water > 0.001) {
            let waterAlpha = Math.min(0.85, 0.25 + water * 0.12);
            let wr = 30, wg = 150, wb = 255;
            if (water > 2.0) { wr = 0; wg = 80; wb = 200; }
            this.ctx.globalAlpha = waterAlpha;
            let grad = this.ctx.createLinearGradient(
                x * this.cellSize, y * this.cellSize,
                (x + 1) * this.cellSize, (y + 1) * this.cellSize
            );
            grad.addColorStop(0, `rgba(${wr},${wg},${wb},${waterAlpha})`);
            grad.addColorStop(1, `rgba(${wr+20},${wg+30},${wb+30},${waterAlpha*0.7})`);
            this.ctx.fillStyle = grad;
            this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
            this.ctx.globalAlpha = 1.0;
        }
    }

    _drawRiskMode(x, y, risk) {
        const totalRisk = Math.max(risk.flood, risk.landslide);
        if (totalRisk > 0.7) {
            this.ctx.fillStyle = `rgb(220, 53, 69)`; // Red for high risk
        } else if (totalRisk > 0.3) {
            this.ctx.fillStyle = `rgb(255, 193, 7)`; // Orange for moderate risk
        } else {
            this.ctx.fillStyle = `rgb(40, 167, 69)`; // Green for low risk
        }
        this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
    }

    _drawInfiltrationPotentialMode(x, y, terrain, baseInfiltrationRate) {
        const effectiveInfiltration = baseInfiltrationRate * (1 - terrain * 0.5); // More infiltration on lower terrain
        let infR, infG, infB;
        // Color gradient from red (low infiltration) to green (high infiltration)
        if (effectiveInfiltration < baseInfiltrationRate * 0.5) {
            infR = 255; infG = Math.floor(255 * (effectiveInfiltration / (baseInfiltrationRate * 0.5))); infB = 0;
        } else {
            infR = Math.floor(255 * (1 - (effectiveInfiltration - baseInfiltrationRate * 0.5) / (baseInfiltrationRate * 0.5))); infG = 255; infB = 0;
        }
        this.ctx.fillStyle = `rgb(${infR},${infG},${infB})`;
        this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
    }    _drawWaterOnlyMode(x, y, water) {
        if (water > 0.001) {
            // Enhanced water visualization with better color gradients
            let waterColorValue = Math.min(1, water / this.MAX_WATER_HEIGHT);
            let r, g, b;
            
            if (waterColorValue > 0.7) {
                // Deep water: dark blue
                r = 0;
                g = 20 + waterColorValue * 80;
                b = 180 + waterColorValue * 75;
            } else if (waterColorValue > 0.3) {
                // Medium water: medium blue
                r = 0;
                g = 100 + waterColorValue * 100;
                b = 200 + waterColorValue * 55;
            } else {
                // Shallow water: light blue
                r = 30 + waterColorValue * 20;
                g = 150 + waterColorValue * 80;
                b = 220 + waterColorValue * 35;
            }
            
            this.ctx.fillStyle = `rgb(${Math.floor(r)},${Math.floor(g)},${Math.floor(b)})`;
        } else {
            this.ctx.fillStyle = `rgb(245, 245, 245)`; // Lighter grey for dry areas
        }
        this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
    }

    _drawCloudsOnlyMode(x, y, cloudDensity) {
        let cloudColorValue = Math.min(1, cloudDensity);
        const r = Math.floor(240 - cloudColorValue * 120);
        const g = Math.floor(240 - cloudColorValue * 120);
        const b = Math.floor(240 - cloudColorValue * 120);
        this.ctx.fillStyle = `rgb(${r},${g},${b})`; // Darker grey for denser clouds
        this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
    }

    _drawContourLines(heightMap) {
        this.ctx.strokeStyle = 'rgba(0, 0, 0, 0.3)';
        this.ctx.lineWidth = 0.8;
        for (let y = 0; y < this.gridResolution; y++) {
            for (let x = 0; x < this.gridResolution; x++) {
                const currentHeight = heightMap[y * this.gridResolution + x];
                // Draw a contour line if the height is close to a multiple of CONTOUR_INTERVAL
                if (Math.abs(currentHeight % this.CONTOUR_INTERVAL) < 0.01 || Math.abs(currentHeight % this.CONTOUR_INTERVAL - this.CONTOUR_INTERVAL) < 0.01) {
                    this.ctx.strokeRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
                }
            }
        }
    }

    _drawClouds(cloudMap, cloudinessFactor) {
        for (let y = 0; y < this.gridResolution; y++) {
            for (let x = 0; x < this.gridResolution; x++) {
                const idx = y * this.gridResolution + x;
                const cloudDensity = cloudMap[idx];
                if (cloudDensity > (1 - cloudinessFactor)) { // Only draw visible clouds
                    const cloudAlpha = Math.min(0.5, (cloudDensity - (1 - cloudinessFactor)) * 2); // More dense = more opaque
                    this.ctx.fillStyle = `rgba(200, 200, 200, ${cloudAlpha})`;
                    this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
                }
            }
        }
    }

    _drawSettlements(settlements, riskMap) {
        settlements.forEach(s => {
            let maxFloodRisk = 0;
            let maxLandslideRisk = 0;
            // Calcular riesgo máximo en el área del asentamiento
            for (let sy = s.y; sy < s.y + s.size; sy++) {
                for (let sx = s.x; sx < s.x + s.size; sx++) {
                    if (sx >= 0 && sx < this.gridResolution && sy >= 0 && sy < this.gridResolution) {
                        const idx = sy * this.gridResolution + sx;
                        if (riskMap[idx]) {
                            maxFloodRisk = Math.max(maxFloodRisk, riskMap[idx].flood);
                            maxLandslideRisk = Math.max(maxLandslideRisk, riskMap[idx].landslide);
                        }
                    }
                }
            }
            const totalMaxRisk = Math.max(maxFloodRisk, maxLandslideRisk);
            // Color y opacidad adaptativos
            let settlementColor, riskIconClass, fallbackEmoji, overlayAlpha;
            if (totalMaxRisk > 0.7) {
                settlementColor = '#dc3545'; overlayAlpha = 0.7;
                riskIconClass = maxFloodRisk > maxLandslideRisk ? 'fas fa-house-damage' : 'fas fa-mountain';
                fallbackEmoji = maxFloodRisk > maxLandslideRisk ? '🏠' : '⛰️';
            } else if (totalMaxRisk > 0.3) {
                settlementColor = '#ffc107'; overlayAlpha = 0.45;
                riskIconClass = maxFloodRisk > maxLandslideRisk ? 'fas fa-tint' : 'fas fa-hill-avalanche';
                fallbackEmoji = maxFloodRisk > maxLandslideRisk ? '💧' : '🪨';
            } else {
                settlementColor = '#28a745'; overlayAlpha = 0.25;
                riskIconClass = 'fas fa-city';
                fallbackEmoji = '🏙️';
            }
            s.riskIcon = riskIconClass;
            // Overlay con bordes suaves
            this.ctx.save();
            this.ctx.globalAlpha = overlayAlpha;
            this.ctx.strokeStyle = settlementColor;
            this.ctx.lineWidth = 2;
            this.ctx.shadowColor = settlementColor;
            this.ctx.shadowBlur = 8;
            this.ctx.fillStyle = settlementColor;
            this.ctx.beginPath();
            this.ctx.roundRect(s.x * this.cellSize, s.y * this.cellSize, s.size * this.cellSize, s.size * this.cellSize, 6);
            this.ctx.fill();
            this.ctx.shadowBlur = 0;
            this.ctx.globalAlpha = 1.0;
            this.ctx.stroke();
            this.ctx.restore();
            // Icono de riesgo
            const iconX = s.x * this.cellSize + (s.size * this.cellSize) / 2;
            const iconY = s.y * this.cellSize + (s.size * this.cellSize) / 2;
            this.ctx.fillStyle = 'black';
            this.ctx.font = `900 ${this.cellSize * 1.5}px "Font Awesome 6 Free", sans-serif`;
            this.ctx.textAlign = 'center';
            this.ctx.textBaseline = 'middle';
            this.ctx.fillText(this._getFontAwesomeChar(riskIconClass, fallbackEmoji), iconX, iconY);
        });
    }

    // Overlay visual para mapa de derrumbes
    _drawLandslideOverlay(riskMap) {
        for (let y = 0; y < this.gridResolution; y++) {
            for (let x = 0; x < this.gridResolution; x++) {
                const idx = y * this.gridResolution + x;
                const landslide = riskMap[idx]?.landslide || 0;
                if (landslide > 0.1) {
                    // Color magenta/rojo para zonas de alto riesgo de derrumbe
                    const alpha = Math.min(0.7, landslide * 0.9);
                    this.ctx.fillStyle = `rgba(220,0,180,${alpha})`;
                    this.ctx.fillRect(x * this.cellSize, y * this.cellSize, this.cellSize, this.cellSize);
                }
            }
        }
    }
}

export { Renderer };